import { AddObjects, AppEventType } from "../../../controllers/AppEventType";
import { sendEvent } from "../../../controllers/storeController";
import { buildBead } from "../../../editorObjects/Bead";
import {
  Bead,
  BeadTypes,
  EditorMode,
  ObjectTypes,
} from "../../../editorObjects/Editor";
import { appStore } from "../../../immerReducers/appStore";
import { Actions } from "../../../immerReducers/store";
import { getModelBBoxFromBeadType } from "../../loadModels";
import { screenToWorld } from "../screenToWorld";
import { MouseEventsBase } from "./MouseEventsBase";

export class MouseEventsEditorModeAddBead extends MouseEventsBase {
  constructor() {
    super();
  }

  onWheel(event: PointerEvent) {}

  onResize(event: PointerEvent) {}

  onDown(event: PointerEvent) {}

  onMove(event: PointerEvent) {}

  onUp(event: PointerEvent) {
    // let defaultBead: Partial<Bead> = {
    //   ...this.draftState.ui.beadsTemplate,
    // };
    // defaultBead.id = undefined;
    // defaultBead.position = buildPoint(x, y);
    // const bead = buildBead(defaultBead);
    const bbox = getModelBBoxFromBeadType(BeadTypes.TOHO_15);
    let width = 1;
    let height = 1;
    let depth = 1;
    if (bbox) {
      width = Math.abs(bbox.max.x - bbox.min.x);
      height = Math.abs(bbox.max.y - bbox.min.y);
      depth = Math.abs(bbox.max.z - bbox.min.z);
    }
    // const { dispatch } = appStore;
    const { x, y } = screenToWorld(event.clientX, event.clientY);
    // dispatch(
    //   Actions.Draggable.action_addBeadOnClick({
    //     x,
    //     y,
    //   })
    // );
    const action1: AddObjects = {
      name: AppEventType.AddObjects,
      payload: {
        objects: [
          {
            position: { x: x, y: y, z: 1 },
            width,
            height,
            objectType: ObjectTypes.Bead,
          },
        ],
      },
    };
    sendEvent(action1);

    // dispatch(
    //   Actions.ChangeEditorMode.action_onChangeEditorMode(EditorMode.SelectOrMove)
    // );
  }
}
