import { buildBead } from "../editorObjects/Bead";
import { AppObject, BeadTypes, RegionShape } from "../editorObjects/Editor";
import { buildGroup } from "../editorObjects/Group";
import { buildPoint } from "../editorObjects/Point";
import { buildPreviewProject3D } from "../editorObjects/PreviewProject3D";
import { buildRegion } from "../editorObjects/Region";
import { getModelBBoxFromBeadType } from "./loadModels";

export const getProjectData = (): Promise<{ objects: AppObject[] }> => {
  return new Promise((resolve, reject) => {
    const objects = {};
    const bbox = getModelBBoxFromBeadType(BeadTypes.TOHO_SPHERE);
    let width = 1;
    let height = 1;
    let depth = 1;
    if (bbox) {
      width = Math.abs(bbox.max.x - bbox.min.x);
      height = Math.abs(bbox.max.y - bbox.min.y);
      depth = Math.abs(bbox.max.z - bbox.min.z);
    }
    for (let i = 0; i < 10 * 10; i = i + 10) {
      for (let j = 0; j < 10 * 50; j = j + 10) {
        const b1 = buildBead({
          width,
          height,
          depth,
          type: BeadTypes.TOHO_SPHERE,
          color: Math.random() > 0.5 ? "red" : "blue",
          position: {
            x: i,
            y: j,
          },
        });
        objects[b1.id] = b1;
      }
    }

    const region = buildRegion({
      height: 100,
      width: 100,
      shape: RegionShape.Rectangle,
      settings: {
        columns: 3,
        rows: 3,
      },
      position: buildPoint(-100, -110),
    });

    objects[region.id] = region;

    const region2 = buildRegion({
      height: 200,
      width: 200,
      shape: RegionShape.Triangle,
      settings: {
        columns: 4,
        rows: 2,
      },
      position: buildPoint(-300, -10),
    });

    objects[region2.id] = region2;

    const group = buildGroup({
      height: 250,
      width: 250,
      position: buildPoint(250, 250),
    });
    const subRegion = buildRegion({
      height: 50,
      width: 50,
      position: buildPoint(200, 200),
      groupId: group.id,
    });
    const subBead = buildBead({
      width,
      height,
      depth,
      type: BeadTypes.TOHO_SPHERE,
      position: buildPoint(300, 300),
      groupId: group.id,
    });
    group.childrenIds = [subBead.id, subRegion.id];
    objects[subBead.id] = subBead;
    objects[subRegion.id] = subRegion;
    objects[group.id] = group;

    const previewProject3D = buildPreviewProject3D({
      height: 50,
      width: 50,
      position: buildPoint(500, 500),
      color: "red",
    });
    objects[previewProject3D.id] = previewProject3D;

    resolve({
      objects: Object.keys(objects).map((id) => {
        return objects[id];
      }),
    });
  });
};
