import { AppObject, Bead } from "../editorObjects/Editor";
import { StoreUiSettings } from "./store";

// PAMIETAJ o tym aby dodac rownież "event do history w AppHistory.ts
export enum AppEventType {
  AddObjects = "AddObjects",
  RemoveObjects = "RemoveObjects",
  ChangeSelectObjects = "ChangeSelectObjects",
  ChangeObjectsProperty = "ChangeObjectsProperty",
  ChangeObjectsAlignment = "ChangeObjectsAlignment",
  ClipboardCut = "ClipboardCut",
  ClipboardCopy = "ClipboardCopy",
  ClipboardPaste = "ClipboardPaste",
  AddMultipleBeads = "AddMultipleBeads",
  ChangeUiSettings = "ChangeUiSettings",
  ChangeUiSettingsZoom = "ChangeUiSettingsZoom",
  ChangeUiSettingsSidebar = "ChangeUiSettingsSidebar",
}

export type AddMultipleBeads = {
  name: AppEventType.AddMultipleBeads;
  payload: { rows: number; columns: number; figure: "rectangle" };
};

export type ClipboardCut = {
  name: AppEventType.ClipboardCut;
  payload: {};
};

export type ClipboardCopy = {
  name: AppEventType.ClipboardCopy;
  payload: {};
};

export type ClipboardPaste = {
  name: AppEventType.ClipboardPaste;
  payload: {
    objects: AppObject[];
  };
};

export enum SnapToLineType {
  X_MIN = "X_MIN",
  X_MAX = "X_MAX",
  X_MIDDLE = "X_MIDDLE",
  Y_MIN = "Y_MIN",
  Y_MAX = "Y_MAX",
  Y_MIDDLE = "Y_MIDDLE",
}

export type ChangeSelectedObjectsAlignment = {
  name: AppEventType.ChangeObjectsAlignment;
  payload: {
    selectedIds: string[],
    snapType: SnapToLineType;
  };
};

export type ChangeUiSettings = {
  name: AppEventType.ChangeUiSettings;
  payload: { uiSettings: Partial<StoreUiSettings> };
};

export type ChangeUiSettingsSidebar = {
  name: AppEventType.ChangeUiSettingsSidebar;
  payload: {
    panel: "objects" | "history" | "comments";
    open: boolean;
    toggle?: boolean;
  };
};

export type ChangeUiSettingsZoom = {
  name: AppEventType.ChangeUiSettingsZoom;
  payload: {
    zoomType: "zoomByScroll" | "zoomInScreenCenter" | "zoomOutScreenCenter";
    useAnimation: boolean;
    clientX?: number;
    clientY?: number;
    scrollDelta?: number;
  };
};

export type AddObjects = {
  name: AppEventType.AddObjects;
  payload: { objects: Partial<AppObject>[] };
};

export type RemoveObjects = {
  name: AppEventType.RemoveObjects;
  payload: { ids: string[] };
};

export type ChangeSelectObjects = {
  name: AppEventType.ChangeSelectObjects;
  payload: { change: { id: string; isSelected: boolean }[] };
};

export type MappedTypeWithNewProperties<T> = {
  [Properties in keyof T]: T[Properties];
};

export type AppObjectKeys = MappedTypeWithNewProperties<Partial<AppObject>>;
export type ChangeObject = {
  id: string;
  properties: AppObjectKeys;
  relatedChanges?: ChangeObject[];
};
export type ChangeObjectsProperty = {
  name: AppEventType.ChangeObjectsProperty;
  payload: {
    changes: ChangeObject[];
  };
};

export type AppEvents =
  | AddObjects
  | RemoveObjects
  | ChangeSelectObjects
  | ChangeObjectsProperty
  | ClipboardCut
  | ClipboardCopy
  | ClipboardPaste
  | AddMultipleBeads
  | ChangeUiSettings
  | ChangeUiSettingsZoom
  | ChangeUiSettingsSidebar
  | ChangeSelectedObjectsAlignment;
