import produce from "immer";
import { cloneDeep } from "lodash";
import {
  addObjectByObjectType,
  triggerDeleteObjectIds,
  updateObjectPosition,
} from "../../threejs/eventEmitterHandlers";
import {
  ChangeObjectsProperty,
  AppEventType,
  ChangeObject,
} from "../AppEventType";
import { ChangeObjectsPropertyHistory } from "../AppHistory";
import { refreshReactState } from "../refreshReactState";
import { setStore, Store } from "../store";
import { StoreObjectHandler } from "../BaseHandlers/StoreObjectHandler";
import dayjs from "dayjs";
import { AppObject, ObjectTypes, Region } from "../../editorObjects/Editor";

export class ChangeObjectsPropertyHandler extends StoreObjectHandler<ChangeObjectsProperty> {
  run() {
    const { changes } = this.event.payload;
    const historyChanges: ChangeObjectsProperty["payload"]["changes"] = [];
    let newStore = produce(this.store, (draft) => {
      changes.forEach((_singleChange) => {
        if (draft.objects[_singleChange.id] === undefined) {
          console.warn("no object found in store", _singleChange.id);
          return;
        }
        const singleHistoryChange: ChangeObject = {
          id: _singleChange.id,
          properties: {},
          relatedChanges: [],
        };
        const objectType = draft.objects[_singleChange.id].objectType;
        const keyNames = Object.keys(_singleChange.properties);
        if (singleHistoryChange !== undefined) {
          keyNames.forEach((key) => {
            singleHistoryChange.properties[key] = cloneDeep(
              draft.objects[_singleChange.id][key]
            );
          });
        }
        keyNames.forEach((key) => {
          draft.objects[_singleChange.id][key] = _singleChange.properties[key];
        });

        if (_singleChange.relatedChanges) {
          _singleChange.relatedChanges.forEach((_singeRelatedChange) => {
            if (draft.objects[_singeRelatedChange.id] === undefined) {
              console.warn("no object found in store", _singeRelatedChange.id);
              return;
            }
            const keyNames = Object.keys(_singeRelatedChange.properties);
            keyNames.forEach((key) => {
              draft.objects[_singeRelatedChange.id][key] =
                _singeRelatedChange.properties[key];
            });
          });
        }

        if (objectType === ObjectTypes.Region) {
          const keyNames = Object.keys(_singleChange.properties);
          const shapeKey: keyof Region = "shape";
          if (keyNames.includes(shapeKey)) {
            // let fistObjectId: string | undefined = undefined;
            // Object.keys(draft.objects).forEach((id) => {
            //   if (draft.objects[id].objectType === ObjectTypes.Bead) {
            //     fistObjectId = id;
            //     return false;
            //   }
            // });
            // if (fistObjectId) {
              // singleHistoryChange.relatedChanges = [
              //   {
              //     id: fistObjectId,
              //     properties: {
              //       position: {
              //         x: -10,
              //         y: -10,
              //       },
              //     },
              //   },
              // ];
            // }
            // zmiana snap dla zaleznych beadsow
            // zmiany w singleHistoryChange.relatedChanges
          }
        }
        historyChanges.push(singleHistoryChange);
      });
    });

    if (this.saveToHistory) {
      const historyEvent: ChangeObjectsPropertyHistory = {
        do: this.event,
        redo: this.event,
        undo: {
          name: AppEventType.ChangeObjectsProperty,
          payload: { changes: historyChanges },
        },
        createdAt: dayjs().toISOString(),
      };
      this.history.push(historyEvent);
    }

    newStore = this.getStoreWithHistory(newStore);

    // @INFO: CHANGES IN THREEJS
    changes.forEach((_singleChange) => {
      this.applyChangesToThree(_singleChange, newStore);

      if (_singleChange.relatedChanges) {
        _singleChange.relatedChanges.forEach((_singeRelatedChange) => {
          this.applyChangesToThree(_singeRelatedChange, newStore);
        });
      }
    });

    newStore = this.getNewStoreWithTopBarPosition(newStore);
    setStore(newStore);
    refreshReactState(newStore);

    // updateObjectsProperty(this.event);

    // refreshReactState(newStore);
  }

  applyChangesToThree(_singleChange: ChangeObject, _store: Store) {
    if (_store.objects[_singleChange.id] === undefined) {
      console.warn("no object found in store", _singleChange.id);
      return;
    }
    const positionKey: keyof AppObject = "position";
    // const objectType = _store.objects[_singleChange.id].objectType;
    const keyNames = Object.keys(_singleChange.properties);
    const isPositionOnly =
      keyNames.length === 1 && keyNames.includes(positionKey) === true;
    if (isPositionOnly) {
      const object = _store.objects[_singleChange.id];
      updateObjectPosition(
        _singleChange.id,
        object.objectType,
        object.position.x,
        object.position.y
      );
    }
    // else if (objectType === ObjectTypes.Region) {
    // const isShapeOnly =
    // keyNames.length === 1 && keyNames.includes("shape") === true;
    // }
    else {
      triggerDeleteObjectIds([
        {
          id: _singleChange.id,
          objectType: _store.objects[_singleChange.id].objectType,
        },
      ]);

      addObjectByObjectType(_store.objects[_singleChange.id]);
    }
  }
}
