export let appThree: AppThree;

export const renderThreejs = (el: HTMLElement | null) => {
  initCanvasApp(el);
};

const initCanvasApp = (el: HTMLElement | null) => {
  if (el === null) {
    console.warn("no html el element found");
    return;
  }

  try {
    initStore();
    initHistory();
    initKeybinding();
    appThree = new AppThree(el);
    el.appendChild(appThree.renderer.domElement);
    loadAppModels(appThree);
    initMouseListeners();
    initEventEmitterHandlers();
    requestAnimationFrame(threeRender);
    startListeningKeys();
    const rect10x10FromScreenToWorld = getRect10x10FromScreenToWorld();
    appThree.camera.userData = { sceneScale: 1, rect10x10FromScreenToWorld };
    subscribeKeys("keyz", (event, pressed) => {
      if (event.ctrlKey || event.metaKey) {
        console.log("listener:: undohistory");
        undoHistory();
      }
      return false;
    });
    subscribeKeys("keyy", (event, pressed) => {
      if (event.ctrlKey || event.metaKey) {
        console.log("listener:: redohistory");
        redoHistory();
      }
      return false;
    });
    subscribeKeys("keyc", (event, pressed) => {
      if (event.ctrlKey || event.metaKey) {
        console.log("listener:: copy");
        clipboardCopy();
      }
      return false;
    });
    subscribeKeys("keyx", (event, pressed) => {
      if (event.ctrlKey || event.metaKey) {
        console.log("listener:: cut");
        clipboardCut();
      }
      return false;
    });
    subscribeKeys("keyv", (event, pressed) => {
      if (event.ctrlKey || event.metaKey) {
        console.log("listener:: paste");
        clipboardPaste();
      }
      return false;
    });

    console.log("co w odpowiedzi?", rectangle(0, 0, 2, 2, {} as any, {
      brickSize: 10,
      brickHeight: 25,
      brickWidth: 20,
      color: "red",
      filled: true,
      palette: [],
    }));
    getProjectData().then((data) => {
      clearThreeScene();

      const store = getStore();

      initThumbnailsGenerator();

      // triggerDeleteObjectIds(
      //   Object.keys(store.objects).map((id) => {
      //     return { id: id, objectType: store.objects[id].objectType };
      //   })
      // );

      const newStore = produce(store, (draft) => {
        draft.objects = {};
        data.objects.forEach((object) => {
          draft.objects[object.id] = object;
        });
        draft.selectedIds = [];
      });

      setStore(newStore);

      store.ui.lights.forEach((light) => {
        addLightFromUI(light, appThree.scene);
      });

      if (store.ui.grid) {
        addGridFromUi(store.ui.grid);
      }

      Object.keys(newStore.objects).forEach((id) => {
        addObjectByObjectType(newStore.objects[id]);
      });
      refreshReactState(newStore);
    });
  } catch (err) {
    console.error("err:", err.toString());
  }
};

export interface AppThreeScene extends THREE.Scene {
  getObjectByUserDataProperty: (
    name: string,
    value: any
  ) => undefined | THREE.Object3D;
}

class AppThree {
  el: HTMLElement;
  camera: THREE.OrthographicCamera;
  scene: AppThreeScene;
  renderer: THREE.WebGLRenderer;
  constructor(el: HTMLElement) {
    this.el = el;
    const { width, height } = el.getBoundingClientRect();

    this.camera = new THREE.OrthographicCamera(
      width / -2,
      width / 2,
      height / 2,
      height / -2,
      0,
      1000
    );

    this.camera.position.set(0, 0, 500);
    this.camera.lookAt(0, 0, 0);

    this.scene = new THREE.Scene() as any;
    this.scene.background = new THREE.Color("#f2f2f2");

    this.renderer = new THREE.WebGLRenderer({
      antialias: true,
      preserveDrawingBuffer: true,
    });
    // this.renderer.setPixelRatio(window.devicePixelRatio);

    this.renderer.setSize(width, height);
  }
}

function threeRender() {
  if (needToResizeRendererToDisplaySize(appThree.renderer)) {
    appThree.camera.updateProjectionMatrix();
  }

  appThree.renderer.render(appThree.scene, appThree.camera);
  requestAnimationFrame(threeRender);
}

function needToResizeRendererToDisplaySize(renderer) {
  const canvas = renderer.domElement;
  const width = canvas.clientWidth;
  const height = canvas.clientHeight;
  const needResize = canvas.width !== width || canvas.height !== height;
  if (needResize) {
    renderer.setSize(width, height, false);
  }
  return needResize;
}

